//根据tag返回listData
import { getBlogList } from "network/blog";
export default {
  data: function data() {
    return {
      tag: "",
      listData: []
    };
  },
  methods: {
    getListData: function getListData(tag) {
      var _this = this;

      return getBlogList(tag).then(function (list) {
        _this.listData = list.data; // console.log(this.listData);
      });
    }
  }
};